.claim__title,
.claim__title--emph {


  font-family: $font-family-serif;
  width: 77%;
  margin: auto;
  text-align: center;
  letter-spacing: .10em;

  @include mq('phone-wide') {
    width: 61%;
  }

  @include mq('tablet') {
    width: 44%;
    max-width: 44%;
    margin: 0;

  }

  @include mq('desktop') {
    letter-spacing: .25em;
    font-weight: 300;
  }
}

.claim__title {
  @include vr($font-size: 2, $line-height:3.25);

  @include mq('phone') {
    @include vr($line-height:3.75);
  }

  @include mq('tablet') {
    @include vr($font-size: 2, $line-height: 5);
  }

  @include mq('desktop') {
    @include vr($font-size: 2, $line-height:5);
  }
}

.claim__title--emph {
  font-size: 3rem;
  @include vr($line-height:3.25);
  color: $accent;
  font-weight: 400;

  @include mq('phone') {
    @include vr($font-size: 3, $line-height:3.75);
  }

  @include mq('tablet') {
    @include vr($font-size: 4, $line-height:5);
  }

  @include mq('desktop') {
    @include vr($font-size: 3, $line-height:5);
  }
}


.claim__title--small {
  display: none;

  @include mq('desktop') {
    display: flex;
    @include vr($font-size: 2, $line-height: 2);
    letter-spacing: .25em;
    font-weight: 300;
    text-transform: uppercase;
  }
}


.claim__link--jump {
  display: flex;
  position: absolute;
  transform: translateY(95%);
