    display: flex;
    position: absolute;
    width: 100%;
    max-width: 100%;
    // height: 50vh;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    flex: 1;
    z-index: 0;
    margin-bottom: 0;
  }
}

.hybrid__background-graphic-element {
  flex: 1;
  max-width: 100%;
  min-height: 0;
  height: 100%;
  border: none;
  background: none;


  >svg {
    width: 100%;
    height: 61vh;
  }

