    background: none;
    fill: darken($deep-gray, $amount: 10%);
  }
}


.hybrid__pagination {
    color: $primary;
  @include mq('tablet') {
    margin-bottom: unset;
  }

  @include mq('desktop') {
    z-index: 1;
